.button {
  text-decoration: none;
  padding: 10px;
  font-family: "Open Sans";
  opacity: 0.6;
  transition: all 0.2 ease-in-out;
  font-size: 12px;
  background-color: transparent;
  outline: none;
  border: 0px;
  cursor: pointer;
  display: flex;
  justify-content: space-around;
}

.navbar-button {
  font-size: larger;
}

.links-wrapper button:hover {
    color: black;
    opacity: 1;
}

.nav-item {
  display: inline;
  margin-right: 20px;
  color: rgb(126, 126, 126);
}

@media only screen and (max-width: 500px) {
  .nav-item {
    display: inline;
    margin-right: 5px;
    color: rgb(126, 126, 126);
  }
}



.nav-item:hover {
  color: black;
  cursor: pointer;
}

