.custom-line.vertical-timeline.vertical-timeline--animate.vertical-timeline--two-columns::before {
    background: gray;
}

.timeline-title {
    margin-top: 40px;
    font-size: 15em;
    text-align: center;
    font-size: 3rem;
}
.project-wrapper {
    width: 80vw;
    margin-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1.project-title {
    font-size: 3rem;
    line-height: 20px;
}

.grid {
    width: 70vw;
    display: grid;
    margin-top: 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    
}

.content {
    opacity: 1 !important;
    text-decoration: none !important;
}

.heading, .heading:link, .header:visited {
    padding-top: 1em;
    font-size: 1,8rem;
    text-align: center;
    color: black;
    text-decoration: none !important;
    transition: 0.3s;
}

.heading:hover {
    font-size: 2rem;
}


