}

.tuiBot-img-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 100vw;
    margin: 0px 1em 1em;
    grid-gap: 0.5em;
    gap: 0.5em;
}

.tuiBot-img {
    width: 100%;
}

.tui-bot-para {
    font-size: 1.5rem;
    line-height: 2rem;
    text-align: justify;
    grid-row-start: 2;
    grid-row-end: 4;
    margin-right: 0.5em;
}

.tui-bot-para.mobile {
