    padding: 0; */
}

html,
body {
  scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    overflow-y: scroll;
    overflow-x: hidden;

}

.section {
    width: 100vw;
    height: auto;
    /* overflow: hidden; */
}

.container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.primary-btn {
    margin: 20px;
    padding: 10px;
    display: inline;
    background-color: black;
    color: white;
    border: 0px;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    cursor: pointer;
    
}

.primary-btn:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}



